.reepay-admin-section-li-header {
	width: 100%;
	font-weight: bold;
}

.reepay-admin-section-li-header-small {
	width: 100%;
	font-weight: bold;
	font-size: small;
	margin-bottom: 0px;
}

.reepay-admin-section-li {
	width: 100%;
}

.reepay-admin-section-li-small {
	width: 100%;
	font-size: small;
	margin-top: 0px;
}

.reepay-full-width, .reepay-full-width .button {
	width: 100%;
	display: block;
	text-align: center;
	clear: both;
}

.reepay-balance {
	margin-bottom: 0;
}

.reepay-balance.last {
	margin-bottom: 5px;
}

.reepay-balance:after {
	clear: both;
	content: "";
	display: table;
}

.reepay-balance__label,
.reepay-balance__amount {
	background-color: #f9f9f9;
	box-sizing: border-box;
	float: left;
	padding: 7px 10px;
	text-transform: uppercase;
	font-size: 10px;
	color: #8a8a8a;
}

.reepay-balance__label {
	font-weight: bold;
	width: 60%;
	border-right: 2.5px solid #FFF;
}
.reepay-balance__amount {
	border-right: 0;
	width: 40%;
	border-left: 2.5px solid #FFF;
	display: -webkit-box;
	display: -ms-flexbox;
	display: -webkit-flex;
	display: flex;
}

.reepay-balance__currency {
	margin-right: 10px;
}

.reepay-balance.last .reepay-balance__currency {
	margin-right: 0;
}

.reepay-admin-card-logo {
	text-align: center; 
	margin-right: 25px;
	max-width: 100px;
}

.reepay-partly_capture_amount, .reepay-partly_refund_amount {
	background-color: #f9f9f9;
	box-sizing: border-box;
	float: left;
	padding: 4px 10px;
	text-transform: uppercase;
	font-size: 10px;
	color: #8a8a8a;
	
	border-right: 0;
	width: 40%;
	border-left: 2.5px solid #FFF;
	display: -webkit-box;  /* OLD - iOS 6-, Safari 3.1-6, BB7 */
	display: -ms-flexbox;  /* TWEENER - IE 10 */
	display: -webkit-flex; /* NEW - Safari 6.1+. iOS 7.1+, BB10 */
	display: flex;         /* NEW, Spec - Firefox, Chrome, Opera */
}

.reepay-capture_partly_amount-field, .reepay-refund_partly_amount-field {
	width: 90px;
	font-size: small;
	max-height: 20px;
	min-height: 1px !important;
	text-align: center; 
}

#payment .wc_payment_methods li label {
	display: flex !important;
	align-items: center;

	.reepay-logos {
		display: flex;
		flex-wrap: wrap;
		margin: 0 0 0 auto;
		justify-content: flex-end;
		max-width: 300px;

		.reepay-logo img {
			margin: .25rem;
		}
	}
}

.capture-item-button{
	margin-top: 7px !important;
}
